.header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	box-sizing: border-box;
	padding: 20px 45px;
	width: 100%;
	@include r(1400) {
		padding: 15px 25px; }
	@include r(1200) {
		padding: 15px; }
	@include r(992) {
		padding: 20px 25px; }
	@include r(768) {
		padding: 20px; }
	&-logo {
		position: relative;
		z-index: 20;
		display: flex;
		justify-content: center;
		align-items: center;
		max-width: 121px;
		max-height: 119px;
		@include r(1600) {
			max-width: 100px; }
		@include r(1400) {
			max-width: 80px; }
		@include r(1200) {
			max-width: 60px; }
		@include r(992) {
			max-width: 100px; }
		@include r(768) {
			max-width: 70px; }
		&__img {
			max-width: 100%;
			max-height: 100%;
			width: auto;
			height: auto; } }
	&-nav {
		@include r(992) {
			position: fixed;
			top: 0;
			left: 0;
			z-index: 15;
			box-sizing: border-box;
			padding-top: 140px;
			padding-bottom: 50px;
			width: 100%;
			height: 100%;
			background-color: $text-dark;
			overflow-y: auto;
			transform: translateY(-100%);
			transition: .25s;
			&.is-show {
				transform: translateY(0); } }
		@include r(768) {
			padding-top: 100px; }
		&-list {
			display: flex;
			margin: 0;
			padding: 0;
			list-style: none;
			@include r(992) {
				flex-direction: column;
				align-items: center; }
			&-li {
				&__link {
					display: block;
					padding: 22px;
					font-family: $lato;
					@include fs(20);
					@include lh(20, 20);
					@include ls(4);
					text-decoration: none;
					color: $text-light;
					transition: .25s;
					@include r(1600) {
						padding: 16px; }
					@include r(1400) {
						padding: 10px;
						@include fs(18); }
					@include r(1200) {
						@include fs(16); }
					&:hover {
						color: $text-dark; } } } }

		&__button {
			display: none;
			@include r(992) {
				position: relative;
				z-index: 20;
				display: block;
				width: 78px;
				height: 50px;
				@include fs(0);
				@include lh(0, 0);
				background-color: transparent; }
			@include r(768) {
				width: 60px; } } } }
